.header {
  padding: 20px 0;
  
}

.navbar {
  padding: 0 20px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}


.logo {
  font-size: 22px;
  font-weight: bold;
  color: #f96424;
  text-decoration: none;
}

.nav-links {
  list-style: none;
  display: flex;
  text-align: right;
}

.nav-links li {
  margin-left: 20px;
}

.nav-links li a {
  color: #4b4949;
  font-weight: 600;
  text-decoration: none;
  transition: color 0.2s ease;
}

.nav-links li a:hover {
  color: #f9ca24;
}
