.blog-post{
    max-width: 980px;
    margin: 0 auto;
    padding: 0 12px;
}

.post-header img{
   height: 500px;
   width: 100%;
   object-fit: cover;
}

.post-body h2{
    font-weight: 500;
}

.post-body p{
    margin: 18px 0;
    font-size: 18px;
    font-weight: 300;
    line-height: 1.78;
}

.post-body strong{
    font-weight: 600;
}

.post-body a {
    color: #0b8484;
    font-weight: 500;
    text-decoration: none;
}
.post-body a:hover{
    text-decoration: underline;
}